import { createComponentFromModel } from 'core/loadEntities';

export default function (entity) {
      const sounds = entity.get('sounds');
      let newSounds={
        name:'sounds',
        scope:'common',
        params:{
          parts:{
            jump:{
              scope:'demo',
              name:'jump0'
            }
          }
        }
      }

      newSounds=createComponentFromModel.call(this,newSounds)
      sounds.parts.jump=newSounds.parts.jump;
}
